.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.navbar {
  padding-left: 5%;
}

.table_row:hover {
  background-color: #faf0cc;
  transition: 0.5s;
}

.page_btn {
  background-color: #1c7ad9;
  color: #faf0cc;
}

.page_btn:hover {
  background-color: #3f7d20;
  color: #faf0cc;
  transition: 0.5s;
}

.navbar-nav {
  margin-left: auto;
  margin-right: 4%;
}

.sin-station-free-space {
  margin-top: 2%;
}

.sin-station-p {
  font-size: 1.2em;
}

.leaflet-container {
  width: 100wh;
  height: 100vh;
}

.nav_item_focus {
  font-size: 1em;
  color: #3c91e6;
}

.main_bg {
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
  min-height: 100vh;
  height: 100%;
  padding-top: 20px;
  width: 100%;
  background: linear-gradient(-45deg, #67aaf9, #7be0d6, #9dc9fb, #cdf3f0);
  color: aliceblue;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

.journey_row {
  color: black;
}

.journey_header {
  font-size: larger;
}

.journey_view {
  background-color: aliceblue;
  border-radius: 15px;
}

.mb-0 {
  color: black;
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.vertical-center {
  margin: 0;
  position: absolute;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

p,
h3,
h4,
h5,
h6 {
  color: #2e2e3a;
}

h1 {
  color: #121217;
}

.nav-item,
.navbar-brand {
  font-size: large;
  font-weight: 500;
}

.spinner-loader {
  width: 80px;
  height: 80px;
  color: #1c7ad9;
  position: relative;
  background: radial-gradient(10px, currentColor 94%, #0000);
}

.spinner-loader:before {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: 50%;
  background: radial-gradient(9px at bottom right, #0000 94%, currentColor) top
      left,
    radial-gradient(9px at bottom left, #0000 94%, currentColor) top right,
    radial-gradient(9px at top right, #0000 94%, currentColor) bottom left,
    radial-gradient(9px at top left, #0000 94%, currentColor) bottom right;
  background-size: 40px 40px;
  background-repeat: no-repeat;
  animation: sp8 3s infinite cubic-bezier(0.3, 1, 0, 1);
}

@keyframes sp8 {
  33% {
    inset: -20px;
    transform: rotate(0deg);
  }
  66% {
    inset: -20px;
    transform: rotate(90deg);
  }
  100% {
    inset: 0;
    transform: rotate(90deg);
  }
}

.infinity-loader {
  width: 20px;
  height: 20px;
  border-radius: 10%;
  background: #1c7ad9;
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  animation: infinityLoader 2s infinite cubic-bezier(0.3, 1, 0, 1);
}

@keyframes infinityLoader {
  33% {
    border-radius: 0;
    background: #e4e4ed;
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }
  66% {
    border-radius: 0;
    background: #1c7ad9;
    clip-path: polygon(50% 0, 50% 0, 100% 100%, 0 100%);
  }
}

.btn_github {
  background-color: whitesmoke;
  color: #121217;
  font-weight: bold;
  border-radius: 10px;
  margin-right: 20px;
  padding: 10px;
}

.btn_github:hover {
  background-color: #f0f3bd;
  color: #1a5e63;
  font-weight: bold;
  border-radius: 10px;
}

.btn_github:visited {
  color: #121217;
}
